      <text class="value">{{ time }}</text>
    </view>
    <view class="section">
      <text class="label">地点</text>
      <text class="value">{{ place }}</text>
    </view>
    <view class="section">
      <text class="label">参与人数</text>
      <text class="value">{{ people }}</text>
    </view>
    <view class="section">
      <text class="label">活动简介</text>
