const people = ref(56);
const desc = ref('围绕校园进行5公里协作跑，提升团队配合和体能耐力。');

onLoad((opt) => {
  id.value = opt.id || '';
  name.value = opt.name || '';
});

const join = () => {
  uni.showToast({ title: '报名成功', icon: 'success' });
};
</script>

